.help {
  background-color: #ffe66d;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.486);
  transition: 0.5s ease-in-out;
  margin: 0 10px;
}

@media screen and (max-width: 950px) {
  div.help {
    margin: 10px 0;
  }
}

@media screen and (max-width: 700px) {
  div.help {
    margin: 0;
  }
}

.help:hover {
  transform: rotateZ(720deg) scale(1.1);
}

.helpContainer  {
  display: flex;
  align-items: center;
}

.contImageUser {
  width: 50%;
}

.letterConnected {
  color: green;
  font-size: 1.2em;
}
